export default {
	name: 'paging',
	props: {
		day: {
			type: Number,
			default: 0
		},
		total: {
			type: Number,
			default: 0
		},
		pageSize: {
			type: Number,
			default: 0
		}
	},
	data() {
		return {
			page: [],
			current: 1
		}
	},
	created: function(e) {
		let that = this
		let total = Number(that.total)
		let pageSize = Number(that.pageSize)
		let zpage = Number(total / pageSize)
		let arr = []
		if (zpage < 5) {
			for (let i = 1; i <= zpage; i++) {
				arr.push(i)
			}
		} else {
			arr = [1, 2, 3, 4, 5]
		}
		that.page = arr
	},
	methods: {
		qy: function(e) {
			let that = this
			that.current = e
			let total = Number(that.total)
			let pageSize = Number(that.pageSize)
			let zpage = Number(total / pageSize)
			let arr = []
			if (zpage < 5) {
				return
			}
			if (e >= zpage - 2) {
				arr = [zpage - 4, zpage - 3, zpage - 2, zpage - 1, zpage]
				that.page = arr
				return
			}
			if (zpage > 5 && e < 4) {
				arr = [1, 2, 3, 4, 5]
				that.page = arr
				return
			}
			if (zpage > 5 && e > 3) {
				arr = [e - 2, e - 1, e, e + 1, e + 2]
				that.page = arr
				return
			}
		}
	},
	onLoad() {

	}
}
